.landing {
  margin-top: 50px;
  display: flex;
  justify-content: center;

  flex-direction: column;
}
.text-hover {
  transition: 0.4s;
}
.text-hover:hover {
  color: rgb(186, 28, 28);
  font-weight: bold;
}
.landing h1 {
  margin:  25px;
  font-family: "GT walsheim", sans-serif;
  font-size: 3.75rem;
  text-align: center;
  background: -webkit-linear-gradient(
    rgb(198, 24, 24),
    rgba(63, 16, 248, 0.368)
  );
  margin-bottom: 9px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.landing .card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.backgroundimg {
  margin: 6px;
  height: 45vh;
  background-image: url("../assets/Braambg.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
}
.second {
  background-image: url("../assets/bgdeux.jpg");
}
.card a {

  background-color: rgb(26, 102, 127);
  color: white;
  padding: 5px;
  text-decoration: none;

}